import { useEffect, useState } from "react";

import { WS_CONFIG } from "../config";

const StatsTable = () => {
  const [stats, setStats] = useState({ team_red: {}, team_blue: {} });

  useEffect(() => {
    const ws = new WebSocket(WS_CONFIG.getStatsWsUrl());
    
    ws.onopen = () => {
      console.log("üîó Stats WebSocket connected");
    };
    
    ws.onclose = () => {
      console.log("üì§ Stats WebSocket disconnected");
    };
    
    ws.onerror = (error) => {
      console.error("‚ùå Stats WebSocket error:", error);
    };
    
    ws.onmessage = (message) => {
      try {
        const data = JSON.parse(message.data);
        
        // Log received data for debugging
        console.log("üìä Stats Data Received:", data);
        
        // Only update stats if the message has team_red and team_blue
        if (data.team_red && data.team_blue) {
          setStats(data);
          
          // Log the actual values being set
          console.log("‚úÖ Stats Updated:");
          console.log(`   üî¥ Red Team  - Kills: ${data.team_red.total_killed || 0}, Bullets: ${data.team_red.bullets_fired || 0}`);
          console.log(`   üîµ Blue Team - Kills: ${data.team_blue.total_killed || 0}, Bullets: ${data.team_blue.bullets_fired || 0}`);
        }
        // Optionally log other messages
        else {
          console.log("‚ö†Ô∏è Received non-stats message:", data);
        }
      } catch (error) {
        console.error("‚ùå Error parsing stats data:", error);
      }
    };
    
    return () => ws.close();
  }, []);

  // Helper function to safely get stat values
  const getStat = (team, stat) => {
    return stats[team]?.[stat] || 0;
  };

  return (
    <div className="summary-card" id="statsCard">
      <h2>Statistical Summary</h2>
      <div className="stats-table-wrapper">
        <table id="statsTable">
          <thead>
            <tr>
              <th>Team</th>
              <th>Killed</th>
              <th>Fired</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Red Team</td>
              <td style={{ 
                fontWeight: 'bold', 
                color: getStat('team_red', 'total_killed') > 0 ? '#ff6666' : 'inherit' 
              }}>
                {getStat('team_red', 'total_killed')}
              </td>
              <td style={{ 
                fontWeight: 'bold', 
                color: getStat('team_red', 'bullets_fired') > 0 ? '#66ff66' : 'inherit' 
              }}>
                {getStat('team_red', 'bullets_fired')}
              </td>
            </tr>
            <tr>
              <td>Blue Team</td>
              <td style={{ 
                fontWeight: 'bold', 
                color: getStat('team_blue', 'total_killed') > 0 ? '#ff6666' : 'inherit' 
              }}>
                {getStat('team_blue', 'total_killed')}
              </td>
              <td style={{ 
                fontWeight: 'bold', 
                color: getStat('team_blue', 'bullets_fired') > 0 ? '#66ff66' : 'inherit' 
              }}>
                {getStat('team_blue', 'bullets_fired')}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
     <style jsx global>{`

body {
    background-color: #0b0c10;
    color: #66fcf1;
    font-family: "Exo 2", sans-serif;
    margin: 0;
    padding: 0;
    min-height: 100vh;
    overflow: hidden;
}

.end-session-button {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: #45a29e;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-family: "Exo 2", sans-serif;
    font-size: 1em;
    transition: background-color 0.3s;
    z-index: 1000;
    text-decoration: none;
}

.end-session-button:hover {
    background-color: #66fcf1;
    color: #0b0c10;
}

#simulationPage {
    width: 100vw;
    height: 100vh;
    background-color: #0b0c10;
    padding: 20px;
    box-sizing: border-box;
    overflow: hidden;
}

.container {
    display: flex;
    height: calc(100vh - 40px);
    gap: 20px;
    flex-wrap: nowrap;
}

.left-container {
    flex: 0 0 200px;
    display: flex;
    flex-direction: column;
    background-color: #1f2833;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(102, 252, 241, 0.5);
    max-height: calc(100vh - 40px);
    overflow: hidden;
}

.team-content-wrapper {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
}

.right-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    min-width: 0;
    height: 100%;
}

.map-container {
    flex: 2;
    position: relative;
    overflow: hidden;
    display: flex;
    min-height: 400px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #1f2833;
    border-radius: 10px;
    width: 100%;
}

.map-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.fullscreen-button {
    position: absolute;
    top: 5px;
    left: 10px;
    max-width: 100px;
    background-color: rgba(69, 162, 158, 0.7);
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    transition: background-color 0.3s;
    z-index: 1;
}

.team-label {
    position: sticky;
    top: 0;
    z-index: 1;
    font-size: 1.2em;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    padding: 10px;
    background-color: #45a29e;
    color: #0b0c10;
    border-radius: 5px;
    margin-bottom: 10px;
}

.soldier-item {
    padding: 5px 10px;
    font-size: 1em;
    color: #66fcf1;
    display: none;
    text-align: left;
    background-color: #0b0c10;
    border-radius: 3px;
    margin-bottom: 5px;
}

.soldier-circle {
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border: 2px solid black;
    z-index: 2;
}

.soldier-line {
    width: 2px;
    height: 20px;
    background-color: white;
    position: absolute;
    top: 0;
    transform-origin: bottom;
}

.soldier-item.visible {
    display: block;
}

.summary-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
}

.summary-card {
    flex: 1;
    padding: 10px;
    text-align: center;
    font-size: 1em;
    box-shadow: 0 0 10px rgba(102, 252, 241, 0.5);
    border-radius: 10px;
    background-color: #1f2833;
    max-height: 300px;
    overflow: hidden;
}

#popupCard {
    flex: 1 1 300px;
    padding: 10px;
    text-align: center;
    font-size: 1em;
    box-shadow: 0 0 10px rgba(102, 252, 241, 0.5);
    border-radius: 10px;
    background-color: #1f2833;
    max-height: 300px;
    overflow: hidden;
    width: 100%;
    max-width: 30%;
}

.popup-message-wrapper {
    max-height: 250px;
    overflow-y: auto;
    padding: 10px;
    word-wrap: break-word;
}

#popupMessage {
    margin: 5px 0;
    padding: 10px;
    font-size: 1em;
    color: #66fcf1;
    background-color: #1b1b1b;
    border-radius: 5px;
    text-align: left;
    width: 100%;
    max-width: 100%;
}

.stats-table-wrapper {
    max-height: 120px;
    overflow-y: auto;
}

#statsTable {
    width: 100%;
    border-collapse: collapse;
}

#statsTable th,
#statsTable td {
    padding: 10px;
    border: 1px solid #66fcf1;
    text-align: left;
}

#statsTable th {
    background-color: #45a29e;
    color: #0b0c10;
}

#statsTable td {
    background-color: #1b1b1b;
}

.team-red-charlie { background-color: red; }
.team-red-delta { background-color: red; }
.team-blue-alpha { background-color: blue; }
.team-blue-delta { background-color: blue; }

.team-content-wrapper::-webkit-scrollbar {
    width: 8px;
}

.team-content-wrapper::-webkit-scrollbar-track {
    background: #1f2833;
    border-radius: 4px;
}

.team-content-wrapper::-webkit-scrollbar-thumb {
    background: #45a29e;
    border-radius: 4px;
}

.team-content-wrapper::-webkit-scrollbar-thumb:hover {
    background: #66fcf1;
}

.image-control-buttons {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    gap: 10px;
    z-index: 10;
}

.image-control-button {
    background-color: rgba(69, 162, 158, 0.7);
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.image-control-button:hover {
    background-color: #66fcf1;
    color: #0b0c10;
}

/* Fullscreen styles */
.map-container.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 1000;
    background-color: #0b0c10;
}

.map-container.fullscreen img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
 `}</style>
    </div>
  );
}    

export default StatsTable;